<template>
    <div class="btn">
        <button class="button" @click="toDescription(productid)">立即购买</button>
    </div>
</template>
<script>
export default {
    props:['productid'],
    data() {
        return {

        }
    },
      methods: {
      toDescription(e) {
          this.$router.push({
              path: "description",
              query:{
                  productid:this.productid
              }
          })
      }
  }
}
</script>
<style lang="scss" scoped>
.button {
  position: fixed;
  bottom: 0;
  background-color: #df8121;
  border-radius: 1rem;
  height: 3rem;
  color: white;
  padding: 0.8rem 8rem;
  margin: 0.6rem 0 1.1rem 0.8rem;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}
</style>


