<template>
    <div class="field">
        <mt-field placeholder="收货人姓名" v-model="username"></mt-field>
         <!-- :attr="{ oninput: 'if(value.length>11)value=value.slice(0,11)' }" -->
        <mt-field placeholder="手机号码" type="number" v-model="tel"></mt-field>
        <mt-field placeholder="收货地址" type="textarea" rows="4" v-model="introduction"></mt-field>
    </div>
</template>
<script>
import { Toast } from "mint-ui";
export default {
  data() {
    return {
      username: "",
      tel: "",
      introduction: "",
      toastError: null //toast提示框
    };
  },
  created() {
    this.getList();
  },
  methods: {
    getList() {
      this.show();
        setTimeout(() => {
          this.toastError.close();
        }, 2000);
    },
    show() {
      this.toastError = Toast({
        message: "亲亲有选项为空白哦~",
        positon: "middle",
        duration: -1,
        iconClass: "icon-cuowutishitubiao",
        className: ""
      });
    },
    

  }
};
</script>
<style lang="scss" scoped>
.field /deep/ {
  margin-bottom: 4.3rem;
}
.field /deep/ input {
  margin-top: -0.6rem;
  font-size: 14px;
//   border-bottom: 1px solid rgba(235, 235, 235, 1);
}

.field /deep/ textarea {
  margin-top: -0.1rem;
  height: 4.5rem;
  font-size: 14px;
//   border-bottom: 1px solid rgba(235, 235, 235, 1);
  overflow: hidden;
}
</style>



