<template>
    <div class="goods-description">
        <div class="goods">
            <img :src="goodsData.image" alt="">
            <div class="description">
                <span>价格：￥{{goodsData.pirce}} <del style="color:#aaa;">￥{{markingPrice}}</del></span>
                <span>已选：{{selectData.attr}}</span>
            </div>
        </div>
        <div class="color-size">
            <div class="color" v-if="goodsData.productAttr" v-for="(item,index) in goodsData.productAttr" :key="index">
                <span>{{item.attr_name}}</span>
                <ul>
                    <li @click="select(attr,index)" :class="selectAttr[index]==attr?'activa':''" v-for="(attr,value_key) in item.attr_values" :key="value_key">{{attr}}</li>
                </ul>
            </div>
            <div class="counts">
                <p>
                    <span>数量</span>
                </p>
                <div class="count-data">
                    <span class="minus" @click="minus">-</span>
                    <span class="number">{{selectData.num}}</span>
                    <span class="add" @click="add">+</span>
                </div>
            </div>
        </div>

        <!-- 手机号等信息 -->
          <div class="field">
        <mt-field placeholder="收货人姓名" v-model="userInfo.username"></mt-field>
         <!-- :attr="{ oninput: 'if(value.length>11)value=value.slice(0,11)' }" -->
        <mt-field placeholder="手机号码" type="number" v-model="userInfo.tel"></mt-field>
        <mt-field placeholder="收货地址" type="textarea" rows="4" v-model="userInfo.introduction"></mt-field>
    </div>
        <!-- 按钮 -->
        <div @click="toPayment">
        <buybtn-x></buybtn-x>
       </div>

    </div>
</template>
<script>
import userinfo from "../../components/userinfo";
import buybtn from "../../components/buybtn";
import axios from "axios";
export default {
  components: {
    userinfoX: userinfo,
    buybtnX: buybtn
  },
  data() {
    return {
      goodsData: "", //数据源
      markingPrice: 0, //划线价
      selectAttr: [], //选择的规格
      productId: "", //商品id

      
      userInfo: {
        username: "",
        tel: "",
        introduction: ""
      },
      selectData: {
        num: 1,
        attr: "",
        price: "",
        unique: "",
      }
    };
  },
  created() {
    this.getInfo();
  },
  methods: {
    getInfo() {
      let productId = this.$route.query.productid;
      this.productId = productId;
      axios
        .get(
          "http://app.taijidjk69.com/api/h5/good/ProductAttrDetail?productId=" +
            parseInt(productId)
        )
        .then(response => {
          this.goodsData = response.data.data;
        })
        .catch(error => {
          console.log(error);
          this.$toast({
                  positon: "middle"
                });
        });
    },

    toPayment() {
      console.log(this.goodsData);
      if (!/^1[34578]\d{9}$/.test(this.userInfo.tel)) {
        this.$toast({
          message: "亲亲手机号码不正确哦~",
          positon: "middle"
        });
        return false;
      }
      if (
        this.userInfo.username.length == 0 ||
        this.userInfo.introduction.length == 0 ||
        this.selectAttr.length !== this.goodsData.productAttr.length
      ) {
        this.$toast({
          message: "亲亲还有未选择的哦~",
          positon: "middle"
        });
        return false;
      }

      // let unique = this.goodsData.productValue.unique;
      // this.unique = unique;
      this;
      let query = {
        cartNum: this.selectData.num,
        productId: this.productId,
        // this.selectData.unique
        // "3df5df21"
        // this.unique
        uniqueId: this.goodsData.unique,
        payType: "weixin",
        name: this.userInfo.username,
        phone: this.userInfo.tel,
        address: this.userInfo.introduction
      };
      console.log(this.unique);
      console.log(this.productId);
      axios
        .get("http://app.taijidjk69.com/api/h5/good/pay", { params: query })
        .then(response => {
          if (response.data.status === 1) {
            let wxpay = {
              order_id: response.data.data.order_id,
              ProductName: response.data.data.ProductName,
              price: response.data.data.price
            };
            axios
              .get("http://app.taijidjk69.com/api/h5/good/wxpayurl", {
                params: wxpay
              })
              .then(res => {
                window.location.href = res.data.mweb_url;
                console.log("获取到支付url");
                // console.log(res);
              })
              .catch(error => {
                console.log(error);
                // this.$toast({
                //   message: "哎呀~开了点小差",
                //   positon: "middle"
                // });
              });
          }
          // console.log(response);
        })
        .catch(error => {
          console.log(error);
          // alert("好嗨呀......");
        });
    },

    select(item, key) {
      console.log(item);
      // key是循环的次数，item是选择规格的名称
      this.selectAttr[key] = item;
      // console.log(this.selectAttr)  this.selectAttr是为了记录客户选择哪些规格   每次点击规格的时候   把规格名称压入这个变量里面
      // 如果循环两次   就可以得到this.selectAttr[0] = 白色， this.selectAttr[1] = 3XL的规格值了

      // 如何确定客户已经选择完了所有规格    只要this.selectAttr长度等于渲染出来的规格数   那就是已经选择完了  this.selectAttr.length == this.goodsData.productAttr.length
      if (this.selectAttr.length == this.goodsData.productAttr.length) {
        this.setPrice(); //设置价格
      } else {
        this.getInfo();
      }
    },
    setPrice() {
      //规格选择完毕设置价格
      let key = "";
      //拼接key值
      for (var i = 0, len = this.selectAttr.length; i < len; i++) {
        key += this.selectAttr[i] + ",";
      }
      key = key.substring(0, key.length - 1);
      this.selectData.attr = key;
  console.log(key)
      this.goodsData.pirce = this.goodsData.productValue[key].price;
      this.markingPrice = this.goodsData.productValue[key].cost;
      this.goodsData.image = this.goodsData.productValue[key].image;
      this.goodsData.unique = this.goodsData.productValue[key].unique;
       console.log(this.goodsData.productValue[key].unique)
    },
    minus() {
      if (this.selectData.num > 1) {
        this.selectData.num--;
      }
    },
    add() {
      this.selectData.num++;
    }
  }
};
</script>
<style lang="scss" scoped>
.goods-description {
  margin-left: 0.2rem;
  padding: 0;
}
.goods {
  display: flex;
  margin-bottom: 1.4rem;
  img {
    width: 7rem;
    height: 7rem;
    border: 4px solid #d6e2f6;
  }
  .description {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin-left: 0.7rem;
  }
  .description span:first-child {
    color: rgba(255, 130, 39, 1);
    line-height: 1.3rem;
  }
  .description span:last-child {
    color: #4c4c4c;
  }
}
.color-size {
  span {
    font-size: 13px;
    color: #4c4c4c;
    margin-top: 0.3rem;
    // border:#ff8227 1px solid;
  }
  .color,
  .size {
    display: flex;
    flex-flow: wrap;
    ul {
      margin: 0;
      padding: 0;
      display: inline-block;
      // border:#ff8227 1px solid;
      li {
        list-style-type: none;
        border: 1px solid #aaa;
        border-radius: 0.22rem;
        color: #aaa;
        padding: 0.28rem 0.45rem;
        margin: 2.1rem 0.5rem 0.2rem 0.1rem;
        font-size: 12px;
        display: inline-block;
      }
      .activa {
        color: #ff8227;
        border: 1px solid #ff8227;
      }
    }
  }

  //   .taglia span {
  //   }
  .counts {
    margin-bottom: 1rem;
    border-bottom: 6px solid #cedaee;
    .count-data {
      display: flex;
      margin-bottom: 1.3rem;
      margin-left: 0.9rem;
      .minus,
      .add,
      .number {
        border: 1px solid #aaa;
        border-radius: 0.22rem;
        color: #aaa;
        padding: 0.28rem 0.65rem;
        margin-left: 0.6rem;
        margin-top: -0.2rem;
      }
      .number {
        padding: 0.28rem 1.45rem;
      }
    }
  }
  .field /deep/ {
    margin-bottom: 4.3rem;
  }
  .field /deep/ input {
    margin-top: -0.6rem;
    font-size: 14px;
    //   border-bottom: 1px solid rgba(235, 235, 235, 1);
  }

  .field /deep/ textarea {
    margin-top: -0.1rem;
    height: 4.5rem;
    font-size: 14px;
    //   border-bottom: 1px solid rgba(235, 235, 235, 1);
    overflow: hidden;
  }
}
</style>



